.nav__menu {
  color: var(--colorWhite);
  background-color: rgba(0, 0, 0, 0);
  justify-content: start;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.375;
  display: flex;
}

.nav__item + .nav__item {
  margin-left: 35px;
}

.nav__link {
  color: inherit;
  padding: 14px 0;
}

.header__menu-btn {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
}

.header__menu-mobile {
  width: 20px;
  height: 12px;
  flex-shrink: 0;
  display: block;
}

@media screen and (min-width: 1200px) {
  .header__menu-mobile {
    display: none;
  }
}

.header__menu-mobile.is-open {
  display: none;
}

.header__backdrop-menu {
  visibility: hidden;
  z-index: 10;
  width: 100vw;
  height: var(--menuTabletHeight);
  background-color: var(--bgcBackDrop);
  transition: visibility var(--durationPrimary) var(--timingPrimary);
  position: absolute;
  top: 0;
  left: 0;
}

.header__backdrop-menu.is-open {
  visibility: visible;
}

.header__menu {
  z-index: 20;
  width: var(--menuMobileWidth);
  height: 100vh;
  background-color: var(--bgcMenuMobile);
  box-shadow: var(--shadowMenuMobile);
  opacity: 0;
  transition: transform var(--durationSecondary) var(--timingSecondary), opacity var(--durationSecondary) var(--timingSecondary);
  position: absolute;
  top: 0;
  left: 100vw;
  overflow: hidden;
  transform: translateX(0);
}

.header__menu.is-open {
  opacity: 1;
  transform: translateX(-100%);
}

.header__menu-close {
  z-index: 20;
  width: 14px;
  height: 14px;
  display: block;
  position: absolute;
  top: 20px;
  right: 20px;
}

@media screen and (min-width: 1200px) {
  .header__menu-close {
    display: none;
  }
}

.nav__header {
